import mod from './index';
import test from 'ava';

test('verifica conversao de numeros', t => {
    mod.converte('I', 1);
    mod.converte('V', 5);
    mod.converte('X', 10);
    mod.converte('L', 50);
    mod.converte('C', 100);
    mod.converte('D', 500);
    mod.converte('M', 1000);
    mod.converte(1000, 'M');
    mod.converte(1, 'I');
    mod.converte(5, 'V');
});
